import React from 'react'
import { connect } from 'react-redux'
import { Input, Form, Checkbox } from 'antd'
import { Button } from 'reactstrap'
import { Link } from 'react-router-dom'
import style from '../style.module.scss'

const mapStateToProps = ({ user, settings, dispatch }) => ({
  dispatch,
  user,
  authProvider: settings.authProvider,
  logo: settings.logo,
})

const Login = ({ dispatch, user }) => {
  const onFinish = values => {
    dispatch({
      type: 'user/LOGIN',
      payload: values,
    })
  }

  const onFinishFailed = errorInfo => {
    console.log('Failed:', errorInfo)
  }

  const onCheck = e => {
    dispatch({
      type: 'user/SET_STATE',
      payload: {
        rememberMe: e.target.checked,
      },
    })
  }

  return (
    <div>
      <div className={`card ${style.loginHeader}`}>
        <div className="media">
          <img
            src="resources/images/logo-worksaurus.png"
            className="mr-3 pr-3 border-right align-self-center"
            alt="Logo"
          />
          <div className="media-body" style={{ lineHeight: '36px' }}>
            <span className="text-dark font-size-18">Enhanched Worksaurus Product Platform</span>
          </div>
        </div>
      </div>
      <div className={`card mb-0 ${style.container}`}>
        <div className="text-dark font-size-24 mb-3">
          <strong>Welcome back,</strong>
          <p className="font-size-16 text-gray-5">Please login using your email and password</p>
        </div>
        <Form
          layout="vertical"
          hideRequiredMark
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          initialValues={{
            email: 'john@kct.co.id',
            password: 'Password#123',
            rememberMe: user.rememberMe,
          }}
        >
          <Form.Item
            name="email"
            label="Email"
            rules={[{ required: true, message: 'Please input your e-mail address' }]}
          >
            <Input size="large" placeholder="Email" />
          </Form.Item>
          <Form.Item
            name="password"
            label="Password"
            rules={[{ required: true, message: 'Please input your password' }]}
          >
            <Input.Password />
          </Form.Item>
          <Form.Item name="rememberMe" valuePropName="checked">
            <Checkbox onChange={onCheck}>Remember Me</Checkbox>
          </Form.Item>
          <Button className="text-center w-100 btn-lg" color="warning">
            <strong>Login</strong>
          </Button>
        </Form>
      </div>
      <div className="text-center pt-2 mb-auto">
        <span className="mr-2">Forgot Password?</span>
        <Link to="/auth/forgot-password" className="kit__utils__link font-size-16">
          Click here
        </Link>
      </div>
    </div>
  )
}

export default connect(mapStateToProps)(Login)
